import React from 'react'
import './Donate.css'

const Donate = () => {
    return(
        <p className="donate-p">Please donate to help the City of Indianapolis maintain our beautiful culture and environment. You can earn 3 credits per dollar by donating to our municipal improvement fund. Please feel free to take part in the manner that is most comfortable for you. Thank you.</p>
    )
}

export default Donate


